import { Helmet } from "react-helmet";


const HeadFont = ({ fonts }) => {
    return fonts && fonts.length > 0 ? <Helmet>
        {fonts.map((font, index) => (
            <link href={`https://fonts.googleapis.com/css?family=${font.family}:${font.variants.join(',')}`} key={index} rel="stylesheet" />
        ))}

    </Helmet> : null
}

export default HeadFont